<template>
  <div class="avaliacao">
    <img
      src="../../assets/5estrelas.png"
      class="column is-2-desktop is-12-mobile is-offset-5"
      alt="cover image"
    >
    <div class="column">
      <div class="title is-3 is-6-desktop level-item" text-align="center">
        Avaliar o(a) {{this.proposta.proposta.user_recebe.user.name}}
        <hr>
      </div>
    </div>
    <br>
    <form action method="POST">
      <div class="columns">
        <div class="column is-offset-3 is-3-desktop is-full-mobile">
          <label for="data" class="label">
            <b>Como foi a atuação dele no geral?</b>
          </label>
          <div class="column">
            <select class="column is-10-desktop inp" name="estrela" id="estrela" v-model="estrela">
              <option :value="5">Excelente</option>
              <option :value="4">Muito Bom</option>
              <option :value="3">Bom</option>
              <option :value="2">Ruim</option>
              <option :value="1">Péssimo</option>
            </select>
          </div>
        </div>
        <div class="column is-3-desktop is-full-mobile">
          <label for="horaInicio" class="label">
            <b>Você recomenda para outras pessoas?</b>
          </label>
          <div class="control">
            <label class="radio">
              <input type="radio" v-model="recomenda" name="recomenda" :value="1">
              Sim
            </label>
            <label class="radio">
              <input type="radio" v-model="recomenda" name="recomenda" :value="0">
              Não
            </label>
          </div>
        </div>
      </div>
      <div class="column is-6-desktop is-offset-3 is-full-mobile">
        <label for="descricao" class="label">
          <b>Descreva mais sobre como foi a atuação dele nesse serviço!</b>
        </label>
        <textarea
          name="descricao"
          id="descricao"
          class="textarea inp"
          v-model="descricao"
          placeholder="Ola terraqueo"
          cols="20"
          rows="5"
        ></textarea>
      </div>
      <div class="control column">
        <button
          @click.prevent="avaliar"
          class="column is-6-desktop is-offset-3 is-full-mobile btn-1"
        >Avaliar profissinal!</button>
      </div>
    </form>
  </div>
</template>

<script>
import propostaForId from "@/graphql/queries/propostaForId.gql";
import addAvaliacao from "@/graphql/mutations/avaliar.gql";
import aceita from "@/graphql/mutations/aceita.gql";

export default {
  data() {
    return {
      proposta: [],
      estrela: 0,
      recomenda: 1,
      descricao: ""
    };
  },
  apollo: {
    proposta: {
      query: propostaForId,
      variables() {
        return {
          id: this.$route.params.id
        };
      },
      update(data) {
        this.proposta = data;
        return this.proposta;
      }
    }
  },
  methods: {
    avaliar() {
      this.$apollo
        .mutate({
          mutation: addAvaliacao,
          variables: {
            estrela: this.estrela,
            recomenda: this.recomenda,
            descricao: this.descricao,
            user_avalia_id: this.proposta.proposta.user_envia.id,
            user_avaliado_id: this.proposta.proposta.user_recebe.id
          }
        })
        .then(() => {
          this.$apollo
            .mutate({
              mutation: aceita,
              variables: {
                id: this.proposta.proposta.id,
                status: 4
              }
            });
          this.$router.push(`/page/cliente/pedidos/`);
        })
        .catch(error => {
          console.error(error);
        });
    }
  }
};
</script>

<style>
@import "../../cssFiles/inputsAndButtons.css";

.inp {
  width: 100%;
  height: 3.5em;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.inp:focus {
  border: 3px solid tomato;
}

.inp1 {
  width: 100%;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.inp1:focus {
  border: 3px solid tomato;
}
</style>
